import wxCharts from '../../components/wx-charts/wxcharts.js';
var _this;
var canvaLineA = null;
var canvaLineB = null;
var canvaArea = null;

var util = require('../../common/util.js');
var getCookie = util.getCookie;
var setCookie = util.setCookie;
var getWarnCookie = util.getWarnCookie;
var changeWarn = util.changeWarn;
var myAjax2 = util.myAjax2;
var backgroundAudioManager = wx.getBackgroundAudioManager();

export default {
    data() {
        return {
            title: '历史记录',
            toast: 0,
            toastTxt: '',
            loading: 0,

            userInfo: null,
            deviceNos: '', // 设备号
            accessToken: null,

            breathNum: '--', // 呼吸值 -100_无效值，其他为正常值
            deviceStatus: null, // 设备状态 3_离床，4_在床，5_光纤故障，6_离线，9_传感器负荷，10_信号弱
            heartNum: '--', // 心率值 65436_无效值，其他为正常值
            markTime: null, // 发生的时间戳
            motionNum: null, // 体动值 0_正常，3_轻微体动，4_中度体动，5_大幅体动，-100_无效值
            timer: null,

            tabNum: '1', // 导航焦点值
            startTime: null, // 开始时间
            endTime: null, // 结束时间
            historyData: [],
            cWidth: '',
            cHeight: '',
            cWidth2: '', //横屏图表
            cHeight2: '', //横屏图表
            pixelRatio: 1,
            serverData: '',
            firstLoad: 0,

            warnNing: 0,
            warningText: '',
            warnNo: '',
            warnDeviceTime: '',
            warnHeartTime: '',
            warnBreathTime: '',
            warnMotionTime: ''
        };
    },
    onLoad() {
        _this = this;
        setTimeout(function() {
            _this.firstLoad = 1;
        }, 2000);
        _this.timer = setInterval(function() {
            util.changeWarn(_this);
            // console.log('图表页面同步一次报警数据')
        }, 1000);











        _this.cWidth = uni.upx2px(750);
        _this.cHeight = uni.upx2px(500);
        _this.cWidth2 = uni.upx2px(700);
        _this.cHeight2 = uni.upx2px(1100);
    },
    onLaunch() {},
    onShow() {
        let _this = this;
        let accessToken = util.getCookie('accessToken');
        let deviceNos = getCookie('deviceNos');
        if (!accessToken) {
            wx.redirectTo({
                url: '../login/index'
            });
        } else if (!deviceNos) {
            uni.redirectTo({
                url: '../code/index'
            });
        } else {
            _this.accessToken = accessToken;
            _this.deviceNos = deviceNos;
            changeWarn(_this, backgroundAudioManager);
            getWarnCookie(_this);
            _this.getTime(1);
            _this.history();
        }
    },
    onHide() {},
    onUnload() {
        clearInterval(this.timer);
    },
    methods: {
        checkWarnState() {},
        linkToLogin() {
            uni.redirectTo({
                url: '../login/index'
            });
        },
        changeNav(type) {
            let tabNum = this.tabNum;
            if (tabNum != type) {
                this.tabNum = type;
                this.getTime(type);
                this.history();
            }
        },
        getTime(page) {
            let timestamp = Date.parse(new Date());
            let result = null;
            if (page == '1') {
                result = this.getTodayStartTime();
            } else if (page == '2') {
                result = parseInt(timestamp / 1000) - 259200;
            } else if (page == '3') {
                result = parseInt(timestamp / 1000) - 604798;
            }
            this.endTime = parseInt(timestamp / 1000);
            this.startTime = result;
        },
        /**
         * 04. 获取设备的历史心率/呼吸/体动数据
         */
        history() {
            this.loading = 1;
            let obj = {
                deviceNos: this.deviceNos,
                startTime: this.startTime,
                endTime: this.endTime
            };
            let _this = this;
            myAjax2(
                'post',
                '/device/physiology/history',
                obj,
                function(res) {
                    if (res.retCode == '10000') {
                        // console.log('history请求成功');
                        _this.setHistoryData(res);
                    } else {
                        // console.log('history未知错误');
                    }
                    // console.log(JSON.stringify(res))
                },
                function(reg) {
                    // console.log(JSON.stringify(reg));
                }
            );
        },
        /**
         * 转换列表类数据
         * @param data
         */
        setHistoryData(data) {
            let successData = data.successData;
            let result = [];
            let chartsTime = [];
            let chartsHeart = [];
            let chartsBreath = [];
            let chartsTidong = [];
            for (let i = successData.length - 1; i > -1; i--) {
                let obj = {
                    heart: successData[i].heart,
                    breath: successData[i].breath,
                    motion: successData[i].motion.substring(0, 1) * 1,
                    time: this.getLocalTime(successData[i].createTime)
                };
                result.push(obj);
                chartsTime.push(this.getLocalTime(successData[i].createTime).substring(0, 11));
                chartsHeart.push(successData[i].heart);
                chartsBreath.push(successData[i].breath);
                chartsTidong.push(successData[i].motion.substring(0, 1) * 1);
            }
            this.historyData = result;
            this.chartsTime = chartsTime;
            this.chartsHeart = chartsHeart;
            this.chartsBreath = chartsBreath;
            this.chartsTidong = chartsTidong;
            let LineA = {
                categories: chartsTime,
                series: [
                    {
                        name: '心率',
                        data: chartsHeart,
                        color: '#1890ff'
                    },
                    {
                        name: '呼吸率',
                        data: chartsBreath,
                        color: '#2fc25b'
                    },
                    {
                        name: '体动值',
                        data: chartsTidong,
                        color: '#facc14'
                    }
                ]
            };
            if (LineA.categories.length > 0 && LineA.series.length > 0) {
                this.showLineB('canvasLineB', LineA);
                this.showArea('canvasArea', LineA);
            }
        },
        /**
         * 格式化时间
         * @param sj
         * @returns {string}
         */
        getLocalTime(sj) {
            let now = new Date(sj * 1000);
            let month = now.getMonth() + 1;
            if (month < 10) month = '0' + month;
            let date = now.getDate();
            if (date < 10) date = '0' + date;
            let hour = now.getHours();
            if (hour < 10) hour = '0' + hour;
            let minute = now.getMinutes();
            if (minute < 10) minute = '0' + minute;
            let second = now.getSeconds();
            if (second < 10) second = '0' + second;
            return month + '-' + date + ' ' + hour + ':' + minute + ':' + second;
        },
        /**
         * 根据当前时间获取今日开始时间点
         */
        getTodayStartTime() {
            let now = new Date(Date.parse(new Date()));
            let year = now.getFullYear();
            let month = now.getMonth() + 1;
            let date = now.getDate();
            let time = year + '/' + month + '/' + date + ' ' + '00:00:01';
            return new Date(time).getTime() / 1000;
        },
        showLineB(canvasId, chartData) {
            canvaLineB = new wxCharts({
                $this: _this,
                canvasId: canvasId,
                type: 'line',
                fontSize: 11,
                legend: true,
                background: '#FFFFFF',
                pixelRatio: _this.pixelRatio,
                rotate: true, //开启图表横屏
                categories: chartData.categories,
                animation: false,
                series: chartData.series,
                xAxis: {
                    disableGrid: true
                },
                yAxis: {},
                width: _this.cWidth2 * _this.pixelRatio,
                height: _this.cHeight2 * _this.pixelRatio,
                dataLabel: false,
                dataPointShape: true,
                extra: {
                    lineStyle: 'curve'
                }
            });
        },
        showArea(canvasId, chartData) {
            canvaArea = new wxCharts({
                $this: _this,
                canvasId: canvasId,
                type: 'area',
                fontSize: 11,
                legend: true,
                background: '#FFFFFF',
                pixelRatio: _this.pixelRatio,
                categories: chartData.categories,
                animation: false,
                series: chartData.series,
                xAxis: {
                    disableGrid: true
                },
                yAxis: {},
                width: _this.cWidth * _this.pixelRatio,
                height: _this.cHeight * _this.pixelRatio,
                dataLabel: false,
                dataPointShape: true
            });
        },
        touchLineA(e) {
            canvaLineA.scrollStart(e);
        },
        moveLineA(e) {
            canvaLineA.scroll(e);
        },
        touchEndLineA(e) {
            canvaLineA.scrollEnd(e);
            //下面是toolTip事件，如果滚动后不需要显示，可不填写
            canvaLineA.showToolTip(e, {
                format(item, category) {
                    return category + ' ' + item.name + ':' + item.data;
                }
            });
        },
        touchLineB(e) {
            canvaLineB.showToolTip(e, {
                format(item, category) {
                    return category + ' ' + item.name + ':' + item.data;
                }
            });
        },
        touchArea(e) {
            canvaArea.showToolTip(e, {
                format(item, category) {
                    return category + ' ' + item.name + ':' + item.data;
                }
            });
        },
        /**
         * 关闭报警
         */
        audioPause() {
            util.audioPause(this, backgroundAudioManager);
        }
    }
};
